












































	import mInput from '../../components/m-input.vue';
var graceChecker = require("../../graceUI/graceChecker.js");
export default {
	data() {
		return {
			pnpre : '86',
			pnpres : ['86', '01', '11', '26', '520'],
			phoneno : '',
			password1:''
			
		}
	},
		components: {
	    mInput
	},

	onShow() {
	
	},
	methods:{
		changePre : function(e){
			this.pnpre = this.pnpres[e.detail.value];
		},
		loginNow : function(e){
			var myreg=/^[1][1,2,3,4,5,7,8,9][0-9]{9}$/;
			if (!myreg.test(this.phoneno)){
				uni.showToast({ title: '请正确填写手机号码', icon : "none"});
			    return false;
			}
			if(this.password1.length == 0){
				uni.showToast({
					title: '请输入密码',
					icon:"none",
					duration: 1500
				});
				return;
			}
			// 表单验证
			var rule = [
				{ name: "pn", checkType: "phoneno", errorMsg: "请填写正确的手机号" },
			
			];
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			// 验证通过
			if (checkRes) {
				var user = uni.getStorageSync("USER_INFO");
				if(this.password1 == user.password){
					uni.setStorageSync("LOGIN_FLAG",'true');
					uni.showToast({
						title: '登录成功',
						icon:'success',
						duration: 1500
					});
					setTimeout(function(){
						var user = uni.getStorageSync("USER_INFO");
						if(user.identity.length != 0)
						{
							uni.switchTab({ 
								url: '../tabbar/tabbar0'
							});
						}
						else{
							uni.navigateTo({
								url:'../checkId/checkId'
							})
						}
					},1500);
				}
				else{
					uni.showToast({
						title: '密码错误',
						icon:'none',
						duration: 1500
					});
					}
			} else {
				uni.showToast({ title: graceChecker.error, icon: "none" });
				return;
			}
		},
		reg : function(){
			uni.navigateTo({
				url:'../register/register'
			})
		}
	}
}
